<template>
    <div>
        <div class="r-pagination-container">
            <div class="r-pagination">
                <div href="" class="r-pagination-previous r-paginate-hashover" :class="listData.current_page === 1 ? 'r-pagination-disable' : '' " @click.prevent="moveCurPage('previous')"> <img :src="require('@/assets/arrow_down.svg')" alt=""  ></div>
                <div class="r-pagination-list">
                    <div class="r-pagination-link r-paginate-hashover"  :class="listData.current_page === 1 ? 'r-pagination-active' : '' " @click.prevent="moveCurPage(1,'number')">1</div>
                    <div class="r-middlePage" v-if="listData.last_page > 2">
                        <div class="r-pagination-ellipsis" v-if="listData.current_page > 2">&hellip;</div>
                        <div class="r-pagination-link r-paginate-hashover r-pagination-active" v-if="listData.current_page > 1 && listData.current_page < listData.last_page">{{listData.current_page}}</div>
                        <div class="r-pagination-link r-paginate-hashover" :class="listData.current_page === listData.current_page + 1 ? 'r-pagination-active' : '' " @click.prevent="moveCurPage(listData.current_page + 1,'number')" v-if="listData.current_page + 1 < listData.last_page">{{listData.current_page + 1}}</div>
                        <div class="r-pagination-link r-paginate-hashover" :class="listData.current_page === listData.current_page + 2 ? 'r-pagination-active' : '' " @click.prevent="moveCurPage(listData.current_page + 2,'number')" v-if="listData.current_page + 2 < listData.last_page">{{listData.current_page + 2}}</div>
                        <div class="r-pagination-ellipsis" v-if="listData.current_page < listData.last_page - 3">&hellip;</div>
                    </div>
                    <div class="r-pagination-link r-paginate-hashover"  :class="listData.current_page === listData.last_page ? 'r-pagination-active' : '' " @click.prevent="moveCurPage('last')" v-if="listData.last_page > 1">{{listData.last_page}}</div>
                </div>
                <div href="" class="r-pagination-next r-paginate-hashover"  :class="listData.current_page === listData.last_page ? 'r-pagination-disable' : '' " @click.prevent="moveCurPage('next')"> <img :src="require('@/assets/arrow_down.svg')" alt=""></div>
            </div>
            </div>
        <v-simple-table fixed-header height="100%" :style="`height: ${maxHeight}px; overflow:auto;`"  v-if="sidebarVersion === 1">
            <template v-slot:default>
            <thead>
                <tr>
                <th class="text-left">Nama Dokumen</th>
                <th class="text-center">Tanggal</th>
                <th class="text-center">Jumlah File</th>
                <th class="text-center">Ketentuan Terkait</th>
                <th class="text-center">Status</th>
                </tr>
            </thead>
            <tbody>
                <tr class="table-content" v-for="(data, index) in listData.data" :key="index" @click="showContent = data.id">
                <td class="table-col1">
                    <div class="r-card2">
                        <div class="r-circle-name" :class="classType(data.category.short_name)">
                            {{data.category.short_name}}
                        </div>
                        <span>Nama Dokumen: {{data.name}}</span>
                    </div>
                </td>
                <td class="table-col2">{{ data.created_at }}</td>
                <td class="table-col3">{{ data.jumlah_file }}</td>
                <td class="table-col4">{{ data.ketentuan_terkait }}</td>
                <td class="table-col5">
                    <div class="r-info-status" v-if="isToday(data.created_at)">
                    Terbaru
                    </div>
                </td>
                </tr>
            </tbody>
            </template>
        </v-simple-table>
        <div class="r-scrollcard"  :style="`height: ${maxHeight-50}px; overflow:auto`" v-if="sidebarVersion === 2">
            <div class="r-card" v-for="(data, index) in listData.data" :key="index" @click="showContent = data.id" :class="showContent === data.id ? 'r-isactive-green' : ''">
                <div class="r-circle-name" :class="classType(data.category.short_name)">
                    {{data.category.short_name}}
                </div>
                <span>Nama Dokumen: {{data.name}}</span>
                <span class="r-new" v-if="isToday(data.created_at)">Terbaru</span>
            </div>
        </div>
    </div>
</template>
<script>
import moment from 'moment/src/moment'

export default {
  props:{
    data: {
      type: Object,
      default: () => ({
        data: []
      })
    },
    sidebarVersion: {
      type: Number,
      default: 1
    }
  },
  computed: {
    maxHeight(){
        if(this.$store.state.isAuthenticated){
            return window.innerHeight - 360;
        }else{
            return window.innerHeight - 280;
        }

    },
    listData(){
      return this.data
    },
    policyDetailId(){
        return this.$store.state.policyDetailId
    }
  },
  watch: {
      policyDetailId(){
        this.showContent = this.policyDetailId
      },
      showContent(){
        this.$store.commit('setPolicyDetailId',this.showContent)
      }
  },
  data: () => ({
      showContent: null
  }),
  methods: {
    classType(type){
          if(type === 'K'){
              return 'r-purple-bg'
          }else if (type === 'MPO'){
              return 'r-blue-bg'
          }else if (type === 'PTO'){
              return 'r-green-bg'
          }else if (type === 'SP'){
              return 'r-red-bg'
          }else{
              return 'r-gray-bg'
          }
    },
     isToday(date){
          if(date === null){
              return true
          }
          var today = moment();
          return today.diff(date, 'days') === 0
      },
    moveCurPage(type, number = null){
       this.$emit('pagination', {type: type, number: number})
    },
  }
}
</script>